body{
    background: #42596F url(_HOST_/img/login/login_bg.jpg) no-repeat center;
    background-size: cover;
    min-width: 300px;
}
.login_box{
    position: absolute;
    top:50%;
    left: 50%;
    color: #333;
    width: 300px;
    padding: 50px 20px 20px;
    border-radius: 20px;
    background: rgba(255,255,255,.8);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    -webkit-box-shadow: 5px 5px 10px #666;
    -moz-box-shadow: 5px 5px 10px #666;
    box-shadow: 5px 5px 10px #666;
    -webkit-animation:loginBox 1.2s;
    animation:loginBox 1.2s;
}
.login_box input,.login_box input:focus{
    background-color: transparent;
}
.login_box input{
    margin: 5px auto;
    border-color: #999;
}
.login_box .wharf{
    position: absolute;
    left: 40%;
    height: 1px;
    background-color: #ddd;
    width: 20%;
    margin-top: -30px;
    padding: 0;
}
.login_box .wharf:after{
    content: '';
    position: absolute;
    width: 50%;
    background-color: rgba(150, 0, 0, 0.5);
    top: -3000px;
    bottom: -5px;
    left: 25%;
    -webkit-box-shadow: 2px 2px 5px #666;
    -moz-box-shadow: 2px 2px 5px #666;
    box-shadow: 2px 2px 5px #666;
    z-index: -1;
}
.icon_username,.icon_password{
    font-size: 20px;
}
.btn_conatiner{
    text-align: right;
    margin-top: 10px;
}

@-webkit-keyframes loginBox
{
    0% {top: -50%;}
    50% {top: 50%;}
    60% {top: 49%;}
    70% {top: 51%;}
    80% {top: 49%;}
    90% {top: 51%;}
    100% {top: 50%;}
}
@keyframes loginBox
{
    0% {top: -50%;}
    50% {top: 50%;}
    60% {top: 49%;}
    70% {top: 51%;}
    80% {top: 49%;}
    90% {top: 51%;}
    100% {top: 50%;}
}

@media (max-width: 768px) {
    .login_box{
        width: 250px;
    }
    .login_box input{
        margin: 0;
    }
    .btn{
        font-size: 14px;
        line-height: 1.2em;
        padding: 6px 12px;
        margin-top: 10px;
    }
}